@font-face {
  font-family: 'icomoon';
  src:  url('asset/font/icomoon.eot?xrryuu');
  src:  url('asset/font/icomoon.eot?xrryuu#iefix') format('embedded-opentype'),
    url('asset/font/icomoon.ttf?xrryuu') format('truetype'),
    url('asset/font/icomoon.woff?xrryuu') format('woff'),
    url('asset/font/icomoon.svg?xrryuu#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
}

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

